  openOptions(index){
    this.ngZone.run(() => {

        switch(index){

          case 1:
            if(this.showOne == false){
              this.showOne = true; this.showTwo = false;
              this.showFour = false;this.showThree = false;          
              this.pageBody.scrollToBottom(300);  
            }
            else{
              this.pageBody.scrollToTop().then(() =>{ 
                  this.showOne = false;  
              });        
            }
            break;

          case 3:
            if(this.showThree == false){
              this.showOne = false; this.showTwo = false;
              this.showFour = false;this.showThree = true;          
              this.pageBody.scrollToBottom(300);  
            }
            else{
              this.pageBody.scrollToTop().then(() =>{ 
                  this.showThree = false;  
              });        
            }
            break;

            case 4:
              if(this.showFour == false){
                this.showOne = false; this.showTwo = false;
                this.showThree = false;this.showFour = true;      
                this.pageBody.scrollToBottom(300);  
              }
              else{
                this.pageBody.scrollToTop().then(() =>{ 
                    this.showFour = false;  
                });        
              }
              break;
        }
    });

   
  }

